<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process227"></a>

<p><br></p>
<p><span class="rvts17">血月岛的秘密（</span><span class="rvts50">The Secrets of Bloodmoon Island</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:367,Y:464</span><span class="rvts21">，血月岛图书馆</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">发现血月岛的秘密</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1300.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">来到距离血月岛传送点不远处的一个废墟里（位置</span><span class="rvts20">X:367,Y:464</span><span class="rvts21">），如果队伍中有角色的智力值（</span><span class="rvts20">Wits</span><span class="rvts21">）为</span><span class="rvts20">22</span><span class="rvts21">点或更高，那么可以发现这里有一个隐藏的地下室。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1301.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">地下室是血月岛的图书馆，在图书馆的书桌上有一本</span><span class="rvts20"></span><span class="rvts21">档案保管员的日志（</span><span class="rvts20">Archivist's Journal</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，这是一本相当重要的书，翻阅它，可以获得血月岛被诅咒古树的名字。</span>
<br><span class="rvts21">另外在图书馆里层，有一处隐藏的石门，石门里有一件</span><span class="rvts20"></span><span class="rvts21">家族宝藏（</span><span class="rvts20">All in Family</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线中的任务物品。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1302.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">，之后与古树对话时，通过呼喊它的名字，可以减轻古树的痛苦，并使它开口说出折磨它恶魔的名字叫做医生达瓦（被恶魔阿爪玛丽克控制）。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



